<template>
  <div class="modal fade" id="FloatModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span v-if="belongModal=='product'"></span>
            <span v-if="belongModal=='coupon'"></span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4" v-if="belongModal=='product'">
              <div class="form-group">
                <label for="image"></label>
                <input type="text" class="form-control" id="image" placeholder="" v-model="item.image">
              </div>
              <div class="form-group">
                <label for="customFile"> 
                  <i class="fas fa-spinner fa-spin" v-if="status.fileLoading"></i>
                </label>
                <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile">
              </div>
              <img :src="item.image" class="img-fluid" alt="">
            </div>
            <div :class="{ 'col-12' : belongModal == 'coupon' , 'col-8' : belongModal == 'product' }">
              <div class="form-group">
                <label for="title"></label>
                <input type="text" class="form-control" id="title" placeholder=""
                  v-model="item.title">
              </div>
              <div class="form-row" v-if="belongModal=='product'">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input type="text" class="form-control" id="category" placeholder="" v-model="item.category">
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input type="unit" class="form-control" id="unit" placeholder="" v-model="item.unit">
                </div>
              </div>
              <div class="form-row" v-if="belongModal=='product'">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input type="number" class="form-control" id="origin_price" placeholder="" v-model="item.origin_price">
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input type="number" class="form-control" id="price" placeholder="" v-model="item.price">
                </div>
              </div>
              <hr v-if="belongModal=='product'">
              <div class="form-group" v-if="belongModal=='product'">
                <label for="description"></label>
                <textarea type="text" class="form-control" id="description" placeholder="" v-model="item.description"></textarea>
              </div>
              <div class="form-group" v-if="belongModal=='product'">
                <label for="content"></label>
                <textarea type="text" class="form-control" id="content" placeholder="" v-model="item.content"></textarea>
              </div>

              <div class="form-row" v-if="belongModal=='coupon'">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input type="number" class="form-control" id="origin_price" placeholder="" v-model="item.percent">
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input type="date" class="form-control" id="price" placeholder="" v-model="item.due_date">
                </div>
              </div>
              <div class="form-row" v-if="belongModal=='coupon'">
                <div class="form-group">
                  <label for="price"></label>
                  <input type="text" class="form-control" id="price" placeholder="" v-model="item.code">
                </div>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" id="is_enabled" v-model="item.is_enabled" :true-value='1'
                    :false-value='0'>
                  <label class="form-check-label" for="is_enabled">
                    
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
          <button type="button" class="btn btn-primary" @click="buildItem"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name:'AdminfloatModal',
    props: ['item', 'isNew', 'click', 'belongModal'],
    data() {
      return {
        status: {
          fileLoading: false
        }
      }
    },
    methods: {
      buildItem() {
        const vm = this;
        let api = `${process.env.VUE_APP_API}/api/${vm.click}/admin/${vm.belongModal}`;
        let method = 'post'
        if (!vm.isNew) {
          api = `${process.env.VUE_APP_API}/api/${vm.click}/admin/${vm.belongModal}/${vm.item.id}`;
          method = 'put'
        }
        this.$http[method](api, {
          data: vm.item
        }).then((response) => {
          console.log("build" + vm.belongModal + "success", response);
          if (response.data.success) {
            $('#FloatModal').modal('hide')
          } else {
            console.log("build" + vm.belongModal + "failed, try again");
            vm.$bus.$emit('message:push', response.data.message, 'danger')
            $('#FloatModal').modal('hide')
          }
          this.$emit('get-items')
        })
      },
      uploadFile() {
        const datafile = this.$refs.files.files[0]
        const vm = this
        const formData = new FormData()
        vm.status.fileLoading = true
        formData.append('file-to-uploads', datafile)
        const api = `${process.env.VUE_APP_API}/api/${vm.click}/admin/upload`;
        this.$http.post(api, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then((response) => {
          console.log("uploadFile", response);
          if (response.data.success) {
            vm.$set(vm.item, 'image', response.data.imageUrl)
          } else {
            console.log("uploadFile failed, try again");
            vm.$bus.$emit('message:push', response.data.message, 'danger')
          }
          vm.status.fileLoading = false
        })
      }
    }
  }
</script>
